/**
 * 
 * @param {Function} func -
 * @param {Number}} gap - 
 */
 export const debounce = ( func, gap ) => {
  let timer
  return function () {
    timer && clearTimeout( timer )
    timer = setTimeout( () => {
      func.apply( this, arguments )
    }, gap )
  }
}
//
export const handlereduce=(arr,key)=>{
  let hash = {};
  var newArr = arr.reduce((item, next) => {
    hash[next[key]] ? '' : hash[next[key]] = true && item.push(next);
    return item
  }, []);
  return newArr
}