 import axios from 'axios';
 import {Message} from 'element-ui';
//  import website from '@/config/website';
//  import {Base64} from 'js-base64';
//  import NProgress from 'nprogress';
//  import 'nprogress/nprogress.css';

var basePath;
try {
    var local = window.location;
    var contextPath = local.pathname.split("/")[1];
    basePath = local.protocol + "//" + local.host + "/" + contextPath;
} catch (e) {}
if(process.env.VUE_APP_NODE_ENV=='production'){
 axios.defaults.baseURL= basePath
}else{
 axios.defaults.baseURL= '/api'
}
 //
 axios.defaults.timeout = 100000;
 //
 axios.defaults.validateStatus = function (status) {
   return status >= 200 && status <= 500;
 };
 //cookie
 axios.defaults.withCredentials = true;
 // NProgress 
//  NProgress.configure({
//    showSpinner: false
//  });
 //http request
 axios.interceptors.request.use(config => {
   // progress bar
  //  NProgress.start();
   const meta = (config.meta || {});
   //headerstext
   if (config.text === true) {
     config.headers["Content-Type"] = "text/plain";
   }
   //headersserializetrue
  //  if (config.method === 'post' && meta.isSerialize === true) {
  //    config.data = serialize(config.data);
  //  }
   return config
 }, error => {
   return Promise.reject(error)
 });
 //http response 
 axios.interceptors.response.use(res => {
   // progress bar
  //  NProgress.done();
   //
  const status = (res.data.code || res.data.code === 0) || res.status;
  //  const statusWhiteList = website.statusWhiteList || [];
   const message = res.data.msg || res.data.error_description || '';
   //catch
  //  if (statusWhiteList.includes(status)) return Promise.reject(res);
   //401
  //  if (status === 401) store.dispatch('FedLogOut').then(() => router.push({path: '/login'}));
  if (status === 401) alert('401')
   // 200   code 1000 
   if (status !== 200 && status !== 1000) {
     Message({
       message: `<section style="line-height: 22px">${message}</section>`,
       type: 'error',
       dangerouslyUseHTMLString: true,
       // duration: 0,
       showClose: true,
     });
     return Promise.reject(new Error(message))
   }
   return res;
 }, error => {
   console.log(error)
  //  NProgress.done();
   return Promise.reject(new Error(error));
 });
 
 export default axios;
 



