function loadDepData ( node ) {
  return loadDepOrUser( node ) // promise
}

function loadUserData ( node ) {
  return loadDepOrUser( node, false )  // promise
}

const defaultOption = {
  tabName: '',  // 
  tabKey: 'dep', // selected eg: {dep: [], role: []}
  children: 'children', // 
  // id 
  nodeId: function ( data ) {
    //data.hasOwnProperty( 'realName' ) ? data.id : data.id
    return  data.id
  },
  //   string | function
  label: function ( data, node ) {
    return data.hasOwnProperty( 'realName' ) ? data.realName : data.name
  },
  //   string | function
  isLeaf: function ( data, node ) {
    return data.hasOwnProperty( 'realName' ) // empID  
  },
  //     
  // function
  searchResTip: function ( data ) {
    //''+id
    return '' + data.departName
  },
  //  
  disabled: function ( data, node ) {
    return false
  },
  //  promise
  onload: loadDepData,
  //  
  onsearch: async function ( searchString, resolve, reject ) {
    // const param = { accountbookId:accountbookId, userName: searchString }
    let employeedata= await getUsersBySearch(accountbookId,searchString)
    resolve( employeedata.data )
  }
}

export const DEP_CONFIG = Object.assign( {}, defaultOption )
// export const ROLE_CONFIG = Object.assign( {}, defaultOption, { tabKey: 'role', tabName: '' } )
export const USER_CONFIG = Object.assign( {}, defaultOption, { tabKey: 'user', tabName: '', onload: loadUserData, disabled: ( data, node ) => !data.hasOwnProperty( 'realName' ) } )
const DEP_USER_CONFIG = Object.assign( {}, defaultOption, { tabKey: 'dep&user', tabName: '', onload: loadUserData, disabled: () => false } )
// const POSITION_CONFIG = Object.assign( {}, defaultOption, { tabKey: 'position', tabName: '' } )
export const CONFIG_LIST = [DEP_CONFIG, USER_CONFIG, DEP_USER_CONFIG]
